import React from 'react';

const logoContainerStyle = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexWrap: 'wrap',
    gap: '5px', // Adjust the gap between logos
    marginTop: '16px', // Adjust the margin top as needed
    marginBottom: '50px',
  };

  const logoStyle = {
    display: 'inline-block',
    // height: '50px', // Set a fixed height for the logos
    margin: '2px 16px', // Adjust margins as needed
  };

const AutoScrollImages = ({ companies, size = '40px' }) => {
  return (
    <div style={logoContainerStyle}>
      {companies.map((logo, index) => (
            <img
              key={index}
              src={logo.src}
              alt={logo.alt}
              style={{ ...logoStyle, height: size }}
            />
          ))}
    </div>
  );
};



export default AutoScrollImages;
