import React, {useState, useEffect} from 'react';
import { theme } from '../../theme.js'
import { scroller } from 'react-scroll';
import AutoScrollImages from './AutoScrollImages';

const Home = () => {

  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  

  const containerStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'space-around',
    // height: isMobile ? null:window.innerHeight,
    height: typeof window !== 'undefined' ? window.innerHeight : 'auto',
    paddingTop: '30px',
  };

  const horizontalSectionStyle = {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    textAlign: 'left',
    // marginBottom: '20px',
    flexWrap: 'wrap',
    // maxWidth: '800px', // Adjust as needed
    // backgroundColor:'blue'
  };

  const textSectionStyle = {
    maxWidth: '500px',
    padding: '20px',
    paddingTop: '0px',
  };

  const titleStyle = {
    fontSize: isMobile ? '1.45rem':'1.9rem',
    fontWeight: 'bold',
    color: theme.colors.primary,
    marginBottom: '-10px',
    // marginTop: '4px',
    fontFamily: theme.fonts.primary,
    // backgroundColor:'blue'
  };

  const subTextStyle = {
    fontSize: '1rem',
    color: '#333',
    fontFamily: theme.fonts.primary
  };

  const imageStyle = {
    width: isMobile ? '150px': '500px',
    marginTop: '50px',
    // backgroundColor:'blue'
  };

  const trustedByStyle = {
    textAlign: 'center',
    fontSize: '1rem',
    // fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    marginBottom:'15px'
  };

  const viewServicesButtonStyle = {
    color: '#fff',
    textDecoration: 'none',
    borderRadius: '5px',
    fontFamily: theme.fonts.primary,
    cursor: 'pointer',
    display:'flex',
    width:140, 
    justifyContent: 'center',
    border: '2px solid #D3D3D3',
    margin: isMobile ? '0 auto':null
  };



  const scrollToServices = () => {
      scroller.scrollTo('services', {
          duration: 800,
          delay: 0,
          offset:-50,
          smooth: 'easeInOutQuart'
      });
    };

  return (
    <div style={containerStyle}>
      <div style={horizontalSectionStyle}>
          <div>
            {typeof window !== 'undefined' &&
              <img src={theme.home.titleLogo} alt="penetration testing services" style={imageStyle} />
            }
          </div>
        <div style={textSectionStyle}>
          <p style={titleStyle}>
          {theme.home.title}
          </p>
          <p style={subTextStyle}>
            {theme.home.description}
          </p>
          <div style={viewServicesButtonStyle} onClick={scrollToServices}>
            <p style={{...subTextStyle, color:'#4E5B6A'}}>View Services</p>
          </div>
        </div>
      </div>
      <div>
        <p style={trustedByStyle}>{theme.home.bottomText}</p>
         <AutoScrollImages companies={theme.home.companies} size={isMobile ? '17px':'35px'}/>
      </div>
    </div>
  );
};

export default Home;
